@import "src/common/styles/variables/variables";

.header {
  position: fixed;
  right: 30px;
  top: 100px;
  bottom: 0;
  z-index: 1;
  display: flex;
  align-items: center;
  height: calc(100vh - 200px);
  opacity: 1;
  transition: opacity 0.3s;
  -webkit-transition: opacity 0.3s;
}

@media (max-width: $tablets) {
    .header{
      display: none;
    }
}